@import "http://fonts.fontstorage.com/import/molot.css";
@import "http://fonts.fontstorage.com/import/roboto.css";


@media screen and (min-height:645px) {
	* {
		padding: 0;
		margin: 0;
		font-family:"Helvetica";
	}

	body {
		text-align: center;
		color: #212121;
		background-color: #212121;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		overflow-y:hidden;

	}

	.header {
		border-bottom: 1px solid #dddcdc;
		padding-bottom: 5px;
		cursor: pointer;
		background-color: white;
		z-index: 99;
	}

	.header h1 {
		text-shadow: 2px 2px #979696;
	}

	.showUpArrow {
		display: block;
		margin: 0 auto;
		font-size: 23px;
		color: #525252;
		margin-bottom: -10px;
	}

	.showDownArrow {
		display: block;
		margin: 0 auto;
		font-size: 23px;
		color: #525252;
		margin-top: -10px;
	}

	.hideArrow {
		display: none;
	}

	.collapsablePanel {
		border: none;
		-webkit-box-shadow: none;
		        box-shadow: none;
		margin-bottom: 0;
	}

	.copyright {
		color: #D9D9D9;
		font-size: 10px;
		margin-top: -10px;
	}

	.nav {
		background-color: white;
		border-right: 1px solid lightgrey;
		height: 100vh;
	}

	.mobileScreenDisplay {
		display: none;
	}

	.deathScreen {
		width: 100%;
		height:742px;
		background-color: #212121;
		position: absolute;
		z-index: 99;
		color: white;
		font-size: 40px;
		padding-top: 25%;
		text-align: center;
		margin-left:-15px;
		margin-top:-742px;
	}

	.terrain {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/floor.png");
	}

	.wall {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/wall.png");
	}

	.player {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/player.png");
	}
	.cop {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/cop.png");
	}
	.door {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/door.png");
	}
	.boss {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/boss.png");
	}
	.fogged {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/fogged.png");
	}
	.weapon {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/box.png");
	}

	.medicine {
		display: inline-block;
		height: 17px;
		width: 17px;
		background-image: url("assets/medicine.png");
	}

	.gameGrid {
		line-height: 8px;
		margin: 0 auto;
		margin-top: -580px;
		width: 616px;
	}

	.progressBarHolder {
		margin-top: 75px;
	}

	.progressBarHolder,
	.experienceBarHolder {
		margin-left: 10px;
		color: #212121;
	}

	.restartButtonHolder {
		margin-top: 30px;
	}
	.insidePanel {
		font-size:15px;
		color:grey;
		padding-left:10%;padding-right:10%;
	}
	.arrowkeys {
		display:block;
		margin: 0 auto;
		background-image: url("assets/arrowkeys.png");
		width:113px;
		height:71px;
	}
	.insidePanel ul, .insidePanel li {
		list-style-type:none;
		text-align:left;
		line-height:20px;
	}	
}

